/*
 * action 
 */
export const SELECT_SUBREDDIT = 'SELECT_SUBREDDIT'
export const INVALIDATE_SUBREDDIT = 'INVALIDATE_SUBREDDIT '
export const REQUEST_POSTS = 'REQUEST_POSTS'
export const RECEIVE_POSTS = 'RECEIVE_POSTS'

/*
 * 
 */

/*
 * Action Creators
 */

//
export function selectSubreddit(subreddit) {
    return {type: SELECT_SUBREDDIT, subreddit}
}

//
export function invalidateSubreddit(subreddit) {
    return {type: INVALIDATE_SUBREDDIT, subreddit}
}

//
export function fetchPostsIfNeeded(subreddit) {
    //  getState() 
    //  dispatch 

    // 
    // 
    return (dispatch, getState) => {
        if (shouldFetchPosts(getState(), subreddit)) {
            //dispatchthunk thunk
            //thunk  dispatch
            return dispatch(fetchPosts(subreddit))
        }
    }
}

/*
 * helper
 */

// 
function shouldFetchPosts(state, subreddit) {
    const posts = state.postsBySubreddit[subreddit]

    if (!posts) {
        return true  // 
    } else if (posts.isFetching) {  // 
        return false
    } else {
        return posts.didInvalidate  //
    }
}


// Action Creator
//1fetchPosts Action Creator 
//2 dispatch  getState  Redux  Action Creator  Action 
//3 Action requestPosts(postTitle)
//4 Action receivePosts(postTitle, json)

//  thunk action 
//  action  
// store.dispatch(fetchPosts('reactjs'))
function fetchPosts(subreddit) {
    // Thunk middleware 
    //  dispatch 
    //  dispatch action
    return (dispatch, getState) => {
        //  dispatch state 
        // API 
        dispatch(requestPosts(subreddit))
        // thunk middleware 
        //  dispatch 

        //  promise
        //  redux middleware 
        return fetch(`http://www.reddit.com/r/${subreddit}.json`).then(response => response.json()).then(json => dispatch(receivePosts(subreddit, json)))
    }
}

function requestPosts(subreddit) {
    return {type: REQUEST_POSTS, subreddit}
}

function receivePosts(subreddit, json) {
    return {
        type: RECEIVE_POSTS,
        subreddit,
        posts: json.data.children.map(child => child.data),
        receivedAt: Date.now()
    }
}



