/*eslint-disable*/
import * as containers from './' //index.js
/*eslint-enable*/

const {App, AsyncApp, TodoApp, IndexComponent, CounterPage} = containers

const Apps = () => (
    <ul>
        <li>
            <Link to="/news/apps/"></Link><Link to="/news/apps//detail"></Link>
        </li>
        <li>
            <Link to="/news/apps/"></Link><Link to="/news/apps//detail">F15-J </Link>
        </li>
    </ul>
);

const News = ({routes, params, children}) => (
    <div>
        <div className="demo-nav">
            <Link to="/news"></Link>
            <Link to="/news/apps"></Link>
        </div>
        {children || ' Page'}
        <Alert style={{
            margin: '16px 0'
        }} message="Alert"/>
        <Breadcrumb separator=">" routes={routes} params={params}/>
    </div>
);

export default(
    <Route component={App}>
        {/* URL */}
        <Route path="/" component={IndexComponent}/>
        <Route path="/select(/:name)" component={AsyncApp}/>
        <Route path="/todo(/:id)" component={TodoApp}/>
        <Route path="/undo" component={CounterPage}/>

        <Route name="news" breadcrumbName="" path="/news" component={News}>
            <Route name="apps" breadcrumbName="" path="apps" component={Apps}>
                <Route name="app" breadcrumbName=":id" path=":id">
                    <Route name="detail" breadcrumbName="ccc" path="detail"/>
                </Route>
            </Route>
        </Route>
    </Route>
)



