/**
 * redux-undoundoableincludeAction
 *  reducercounterfilter action
 * action INCREMENT_COUNTER, DECREMENT_COUNTER,
 * limit debug undotype  redotype action
 */
import undoable, { includeAction } from 'redux-undo'

/**
 * reducers
 */
import {visibilityFilter, todos} from './todo'
import {selectedSubreddit, postsBySubreddit} from './post'
import {counter} from './counter'

/**
 * actions const
 */
import {
    INCREMENT_COUNTER,
    DECREMENT_COUNTER,
    UNDO_COUNTER,
    REDO_COUNTER
} from '../actions'

// Reducer  Reducer
// State  Reducer 
// ES6  state  key  import 
// ES6 
const rootReducer = combineReducers({
    postsBySubreddit,
    selectedSubreddit,
    visibilityFilter,
    todos,
    counter: undoable(counter, {
        //filteraction
        filter: includeAction([
            INCREMENT_COUNTER,
            DECREMENT_COUNTER
        ]),
        limit: 10,  // 
        // debug: true,  //
        undoType: UNDO_COUNTER,  // action
        redoType: REDO_COUNTER  // action
    })
})

export default rootReducer



